<template>
    <div id="articleList">
        <articles v-for='article in articleList' :key='article.pk' :info='article.fields' :pk='article.pk'></articles>
    </div>
</template>
<script>
import articles from '@/components/articles';
export default {
    name:'articleList',
    components:{
        articles
    },
    data(){
        return{
            articleList:null
        }
    },
    created(){
        this.getArticles('1')
    },
    methods:{
        getArticles(n){
                this.axios({
                    url:'http://106.52.25.130:9999/api/articleList',
                    method:'get',
                    params:{page:n}
                })
                .then(res=>{
                    this.articleList=res.data;
                    })
                .catch(error=>{console.log(error)})
            }
        }

}

</script>
<style scoped>
#articleList{
    height: fit-content;
    width: 85%;
    padding: 2em 0;
}
</style>


