<template>
    <div id="main" :style="randomColor()">
    <article>
        <header><h3>{{title}}</h3></header>
        <section v-html="preview"></section>
        <footer>
            <span>{{time}}</span>
            <div><router-link class="fixedlinks" :to="apiUrl+id">Read More</router-link></div>
        </footer>
    </article>
    </div>
</template>
<script>
export default {
    name:'articles',
    props:['info','pk'],
    data(){
        return{
            apiUrl:'/article/detail/',
            title:this.info.title,
            preview:this.info.articlePreface,
            time:this.info.createdTime,
            id:this.pk
        }
    },
    methods:{
        randomColor(){
            var colorList=['#a8d8b9','#dac9a6','#f596aa','#a5dee4','#a5dee4','#7b90d2']
            var theColor=colorList[Math.floor(Math.random()*colorList.length)]
            return 'background:'+theColor
        }
    },
    
    
}
</script>
<style scoped>
#main{
    width: 90%;
    height:fit-content ;
    margin: 2em auto;
    padding: 1em 1em;
    position: relative;
    border-radius:1em 2em 1em;
    box-shadow: 1px 1px 1px 1px #333333;
    transition: 0.3s;
}
#main:hover{
    box-shadow: 1px -1px 5px 1px #333333;
}
footer{
    position: relative;
}
footer div{
    position:absolute;
    right: 1em;
    bottom: 0.5em;
}


</style>


